@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Allison&display=swap);
.nav-container {
  display: flex;
  box-sizing: border-box;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Akaya Kanadaka", cursive;
  background: linear-gradient(
    to right,
    #387cff,
    #72a2ff,
    #8bcdff,
    #72a2ff,
    #387cff
  );
  font-size: 15px;
}
.logo {
  font-size: 50px;
  background: -webkit-linear-gradient(rgb(0, 0, 0), rgb(110, 7, 179));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 20px;
  cursor: pointer;
  justify-self: start;
  font-family: "Sansita Swashed", cursive;
}
h2 {
  font-family: "Zen Dots", cursive;
}
.invoiceno {
  display: flex;
}
.textbox {
  height: 25px;
  width: 120px;
  margin: 15px;
}

.address-container {
  margin: 10px;
}
.alignadd {
  display: flex;
}
.textbox {
  height: 25px;
  width: 400px;
  margin: 15px;
}
.countrysel {
  margin: 20px;
}
.backcolor {
  background: rgb(255, 255, 255);
}
.h3 {
  background: #fff;
  padding: 2px;
  border-radius: 10px;
}
legend {
  font-size: 25px;
  font-weight: 600;
}
fieldset {
  padding: 10px 60px;
}

table {
  margin: 10px;
  font-size: large;
  border: 2px;
  border-radius: 10px;
  width: 80%;
  padding: 10px;
}
table {
  background-color: cornflowerblue;
}
td {
  background-color: #ffffff;
  border: 2px;
  border-radius: 5px;
  border: 2px;
  border-radius: 10px;
  font-weight: lighter;
}
th,
td {
  border: 2px;
  border-radius: 5px;
  font-weight: bold;
  padding: 20px;
  text-align: center;
  margin: 10px;
}
.tablecontainer {
  display: flex;
  justify-content: center;
}

.btn {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: linear-gradient(to right, #2f53f7, #6894f5);
  padding: 7px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
  font-family: "Akaya Kanadaka", cursive;
  width: 100px;
  /* margin-left: 220px; */
}
.btn:hover {
  background: #434343;
  letter-spacing: 1px;
  box-shadow: 5px 40px -10px rgba(0, 0, 0, 0.57);
  transition: all 0.4s ease 0s;
}
.totalcontainer {
  display: flex;
  justify-content: flex-end;
}
.subtotalcontainer {
  padding: 5px;
}
.btnadd {
  margin-left: 220px;
}
.subtotal,
.taxcontainer {
  padding: 10px;
  /* display: flex;
  justify-content: space-between; */
}
.btnsub {
  width: 150px;
}
.textbox {
  padding: 5px;
  width: 150px;
}
.text {
  font-family: "Oswald", sans-serif;
  font-size: 25px;
  font-weight: bold;
}
.containermain {
  border-top: 2px solid rgb(189, 189, 189);
  border-bottom: 2px solid rgb(189, 189, 189);
  display: flex;
  justify-content: space-between;
  width: 400px;
  margin-right: 20px;
  margin: 10px;
}
.finaltotalcontainer {
  display: flex;
  justify-content: space-between;
  width: 400px;
  margin-right: 20px;
  margin: 10px;
  border-top: 2px solid rgb(189, 189, 189);
  border-bottom: 2px solid rgb(189, 189, 189);
}
.text1 {
  font-family: "Oswald", sans-serif;
  font-size: 30px;
  font-weight: bold;
}

body {
  padding: 0px;
  margin: 0px;
  background-color: rgb(255, 255, 255);
}

